import React, { useState, useEffect } from 'react';

import { Select, Row, Col, Button, Modal, Form, Input, DatePicker, Upload, message } from 'antd';
import { AppstoreAddOutlined, UploadOutlined  } from '@ant-design/icons';

import moment from 'moment';

import CourseItem from './CourseItem';
import { connect } from 'react-redux';
import * as action from '../../../redux/actions';
import axios from 'axios';

const { Option } = Select;

const ManagerCourses = ({ courses, createCourseAPI }) => {
    // modal
    const [modalStyle, setModalStyle] = useState({
        visible: false,
        loading: false,
    });
    const { visible, loading } = modalStyle;
    const dateFormat = 'DD/MM/YYYY';

    const [teacherList, setTeacherList] = useState([]);

    const getTeacher = (token) => {
        return axios({
            url:'https://courses-project-api.herokuapp.com/getTeacher',
            method:'GET',
            headers: {
                'Authorization': token,
                "Content-Type": "application/json",
            },
        }).then(res => {
            setTeacherList([...res.data.teachers]);
        }).catch(err => console.log(err))
    };

    const renderTeacher = () => {
        return teacherList.map((item,index) => {
            return  <Select.Option value={item._id} key={index}>{item.name}</Select.Option>
        })
    };

    const showModalAddCourse = () => {
        getTeacher(JSON.parse(localStorage.getItem('token')).token);
        setModalStyle({
            ...modalStyle,
            visible: true,
        });
    };

    const handleCancel = () => {
        setModalStyle({
            ...modalStyle,
            visible: false,
        });
    };

    const formItemLayout = {
        labelCol: {
          xs: { span: 24 },
          sm: { span: 7 },
        },
        wrapperCol: {
          xs: { span: 24 },
          sm: { span: 17 },
        },
    };

    const [form] = Form.useForm();


    const onFinish = values => {
        console.log('Received values of form: ', values['dateCourse'].format('DD/MM/YYYY'));
        createCourseAPI(JSON.parse(localStorage.getItem('token')).token,{
            ...values,
            dateCourse: values['dateCourse'].format('DD/MM/YYYY')
        });

        setModalStyle({
            ...modalStyle,
            loading: true,
        });
        setTimeout(() => {
            setModalStyle({
                ...modalStyle,
                visible: false,
                loading: false,
            });
        }, 2000);
    };

    useEffect(() => {
        renderCourses();
    },[courses.length]);

    // end Modal

    const [searchType, setSeachType] = useState("");

    const onChange = (value) => {
        console.log(`selected ${value}`);
        setSeachType(value);
    };

    const renderNameCourse = () => {
        return courses.map((item,index) => {
            return <Option value={item.name} key={index}>{item.name}</Option>
        })
    }

    const onChangeName = (value) => {
        setSeachType(value);
    };

    const renderCourses = () => {
        if(searchType !== "FE" && searchType !== "BE"){
            return courses.map((item,index) => {
                if(item.name.toLowerCase().trim().indexOf(searchType.toLowerCase().trim()) !== -1) {
                    return  <Col span={6} key={index}>
                                <CourseItem item={item}/>
                            </Col>
                }
            })
        }
        return courses.map((item,index) => {
            if(item.type.toLowerCase().trim().indexOf(searchType.toLowerCase().trim()) !== -1) {
                return  <Col span={6} key={index}>
                            <CourseItem item={item}/>
                        </Col>
            }
        })
    };

    return (
        <div className="manager__courses">
            <h1>QUN L KHA HC</h1>
            <div className="manager__courses__control">
                <Row gutter={[16,16]}>
                    <Col span={9}>
                        <Select
                            showSearch
                            placeholder="Tm kim theo loi"
                            optionFilterProp="children"
                            onChange={onChange}
                            filterOption={(input, option) =>
                                option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                            }
                            className="search__courses"
                            size="large"
                        >
                            <Option value="">Tt c</Option>
                            <Option value="FE">Front-End</Option>
                            <Option value="BE">Back-End</Option>
                        </Select>
                    </Col>
                    <Col span={9}>
                        <Select
                            showSearch
                            placeholder="Tm kim theo tn"
                            optionFilterProp="children"
                            onChange={onChangeName}
                            filterOption={(input, option) =>
                                option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                            }
                            className="search__courses"
                            size="large"
                        >
                            <Option value="">Tt c</Option>
                            {renderNameCourse()}
                        </Select>
                    </Col>
                    <Col span={6}>
                        <Button type="primary" danger shape="round" icon={<AppstoreAddOutlined />} size="large" style={{width:'90%',marginLeft:'10%'}} onClick={showModalAddCourse}>
                            THM KHA HC
                        </Button>
                    </Col>
                </Row>
                <Row gutter ={[16,16]}>
                    {renderCourses(searchType)}
                </Row>
            </div>

            <Modal
                title="Thm kha hc"
                visible={visible}
                footer=""
                onCancel={handleCancel}
                className="modal__addCourse"
                >
                <Form
                    {...formItemLayout}
                    form={form}
                    name="register"
                    onFinish={onFinish} 
                    scrollToFirstError
                >
                    <Form.Item
                        name="nameCourse"
                        label="Tn kha hc"
                        rules={[
                        {
                            type: 'string',
                            message: 'Nhp tn kha hc',
                        },
                        {
                            required: true,
                            message: 'Vui lng nhp tn kha hc!',
                        },
                        ]}
                    >
                        <Input placeholder="Nhp tn kha hc"/>
                    </Form.Item>
                    <Form.Item
                        name="typeCourse"
                        label="Loi kha hc"
                        rules={[
                        {
                            type: 'string',
                            message: 'Chn loi kha hc',
                        },
                        {
                            required: true,
                            message: 'Vui lng chn loi kha hc!',
                        },
                        ]}
                    >
                        <Select placeholder="Chn loi kha hc">
                            <Select.Option value="FE">Front-End</Select.Option>
                            <Select.Option value="BE">Back-End</Select.Option>
                        </Select>
                    </Form.Item>
                    <Form.Item 
                        name="dateCourse" 
                        label="Thi gian to" 
                        rules={[
                            {
                                required: true,
                                message: 'Vui lng chn thi gian c th!',
                            }
                        ]}
                    >
                        <DatePicker format={dateFormat} defaultValue={moment(new Date(),dateFormat)}/>
                    </Form.Item>
                    <Form.Item name="uploadCourse" label="Hnh nh" rules={[
                        {
                            type: 'string',
                            message: 'Chn hnh nh',
                        },
                        {
                            required: true,
                            message: 'Vui lng chn hnh nh!',
                        },
                        ]}>
                        <Input placeholder="Nhp link nh"/>
                    </Form.Item>
                    <Form.Item name="descCourse" label="M t" rules={[
                        {
                            type: 'string',
                            message: 'Nhp m t kha hc',
                        },
                        {
                            required: true,
                            message: 'Vui lng nhp m t kha hc!',
                        },
                        ]}>
                        <Input placeholder="Nhp m t kha hc"/>
                    </Form.Item>
                    <Form.Item name="authorCourse" label="Ging vin" rules={[
                        {
                            type: 'string',
                            message: 'Chn ging vin',
                        },
                        {
                            required: true,
                            message: 'Vui lng chn ging vin!',
                        },
                        ]}>
                        <Select placeholder="Chn ging vin">
                            {renderTeacher()}
                        </Select>
                    </Form.Item>
                    <Form.Item>
                        <Button key="back" onClick={handleCancel}>
                            Thot
                        </Button>
                        <Button htmlType="submit" key="submit" type="primary" loading={loading}>
                            To
                        </Button>
                    </Form.Item>
                </Form>
            </Modal>
        </div>
    )
}

const mapStateToProps = state => {
    return {
        courses: state.courses
    }
}

const mapDispatchToProps = (dispatch, props) => {
    return {
        createCourseAPI: (token,data) => {
            dispatch(action.createCourseAPI(token,data));
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ManagerCourses);
