import React,{useState, useEffect} from 'react';

import {
    Form,
    Input,
    Select,
    Checkbox,
    Button,
    message
} from 'antd';
import { Link, Redirect } from 'react-router-dom';
import axios from 'axios';

export const RegisterComponent = () => {
    const [messageSignUp, setMessageSignUp] = useState("");
    const { Option } = Select;

    const formItemLayout = {
        labelCol: {
            xs: { span: 24 },
            sm: { span: 6 },
        },
        wrapperCol: {
            xs: { span: 24 },
            sm: { span: 18 },
        },
    };
    const tailFormItemLayout = {
        wrapperCol: {
            xs: {
            span: 24,
            offset: 0,
            },
            sm: {
            span: 16,
            offset: 8,
            },
        },
    };

    const [form] = Form.useForm();


    const success = () => {
        message.success('To ti khon thnh cng');
    };
    const error = () => {
        message.error('Ti khon  tn ti!');
    };

    const onFinish = values => {
        setMessageSignUp("");
        axios({
            method:"POST",
            url:'https://courses-project-api.herokuapp.com/signup',
            data: values
        }).then(res => {
            console.log(res.data.message);
            if(res.data.message === 'success') {
                success();
                setMessageSignUp(res.data.message);
            } else {
                error();
            }
        }).catch(err => console.log(err));
    };

    const handleRedirect = () => {
        return <Redirect to="/signin" />
    }
    if(messageSignUp === 'success') return handleRedirect();
    
    const prefixSelector = (
        <Form.Item name="prefix" noStyle>
        <Select style={{ width: 70 }}>
            <Option value="86">+84</Option>
            <Option value="87">+87</Option>
        </Select>
        </Form.Item>
    );

    return (
        <div className="register">
            <div className="register__wrapper">
                <h1>ng k</h1>
                <Form
                    {...formItemLayout}
                    form={form}
                    name="register"
                    onFinish={onFinish}
                    initialValues={{
                        prefix: '84',
                    }}
                    scrollToFirstError
                >
                    <Form.Item
                        name="username"
                        label="Tn ti khon"
                        rules={[
                        {
                            type: 'string',
                            message: 'Vui lng nhp tn ti khon!',
                        },
                        {
                            required: true,
                            message: 'Vui lng nhp tn ti khon!',
                        },
                        ]}
                    >
                        <Input />
                    </Form.Item>

                    <Form.Item
                        name="password"
                        label="Mt khu"
                        rules={[
                        {
                            required: true,
                            message: 'Vui lng nhp mt khu!',
                        },
                        ]}
                        hasFeedback
                    >
                        <Input.Password />
                    </Form.Item>

                    <Form.Item
                        name="confirm"
                        label="Xc thc mt khu"
                        dependencies={['password']}
                        hasFeedback
                        rules={[
                        {
                            required: true,
                            message: 'Vui lng nhp li mt khu!',
                        },
                        ({ getFieldValue }) => ({
                            validator(rule, value) {
                            if (!value || getFieldValue('password') === value) {
                                return Promise.resolve();
                            }
                            return Promise.reject('Xc thc khng chnh xc!');
                            },
                        }),
                        ]}
                    >
                        <Input.Password />
                    </Form.Item>

                    <Form.Item
                        name="name"
                        label="H tn"
                        rules={[
                        {
                            type: 'string',
                            message: 'Vui lng nhp h tn!',
                        },
                        {
                            required: true,
                            message: 'Vui lng nhp h tn!',
                        },
                        ]}
                    >
                        <Input />
                    </Form.Item>

                    <Form.Item
                        name="phone"
                        label="S in thoi"
                        rules={[{ required: true, message: 'Vui lng nhp s in thoi!' }]}
                    >
                        <Input addonBefore={prefixSelector} style={{ width: '100%' }} />
                    </Form.Item>

                    <Form.Item
                        name="address"
                        label="a ch"
                        rules={[
                        {
                            type: 'string',
                            message: 'Vui lng nhp a ch!',
                        },
                        {
                            required: true,
                            message: 'Vui lng nhp a ch!',
                        },
                        ]}
                    >
                        <Input />
                    </Form.Item>

                    <Form.Item
                        name="agreement"
                        valuePropName="checked"
                        rules={[
                        { validator:(_, value) => value ? Promise.resolve() : Promise.reject('Should accept agreement') },
                        ]}
                        {...tailFormItemLayout}
                    >
                        <Checkbox>
                        I have read the <Link to="">agreement</Link>
                        </Checkbox>
                    </Form.Item>
                    <Form.Item {...tailFormItemLayout}>
                        <Button type="primary" htmlType="submit">
                            ng k
                        </Button>
                    </Form.Item>
                </Form>
            </div>
        </div>
    )
};
