import React from 'react';
import { Form, Input, Button, Checkbox, message } from 'antd';
import { UserOutlined, LockOutlined } from '@ant-design/icons';
import { Link, Redirect } from 'react-router-dom';

import { connect } from 'react-redux';
import * as actions from '../../redux/actions/index';
import axios from 'axios';

const LoginComponent = ({handleLogin}) => {
    const successLogin = () => {
        message.success('ng nhp thnh cng');
    }

    const errorLogin = () => {
        message.error('Ti khon hoc mt khu khng ng');
    }

    const onFinish = values => {
        axios({
            method:"POST",
            url:'https://courses-project-api.herokuapp.com/signin',
            headers: {
                "Content-Type": "application/json",
            },
            data: {
                username: values.username,
                password: values.password
            }
        })
        .then(res => {
            if(res.data.token) {
                localStorage.setItem('token',JSON.stringify(res.data));
                handleLogin(res.data.token);
                successLogin();
                return <Redirect to="/"/>
            }
            return errorLogin();
        })
        .catch(err => {
            console.log(err);
        });
    };

    return (
        <div className="login">
            <div className="login__wrapper">
                <h1>ng nhp</h1>
                <Form
                    name="normal_login"
                    className="login-form"
                    initialValues={{ remember: true }}
                    onFinish={onFinish}
                >
                    <Form.Item
                        name="username"
                        rules={[{ required: true, message: 'Vui lng nhp tn ti khon!' }]}
                    >
                        <Input prefix={<UserOutlined className="site-form-item-icon" />} placeholder="Nhp tn ti khon" />
                    </Form.Item>
                    <Form.Item
                        name="password"
                        rules={[{ required: true, message: 'Vui lng nhp mt khu!' }]}
                    >
                        <Input.Password
                        prefix={<LockOutlined className="site-form-item-icon" />}
                        type="password"
                        placeholder="Nhp mt khu"
                        />
                    </Form.Item>
                    <Form.Item>
                        <Form.Item name="remember" valuePropName="checked" noStyle>
                        <Checkbox>Remember me</Checkbox>
                        </Form.Item>

                        <Link className="login-form-forgot" to="/">
                        Qun mt khu?
                        </Link>
                    </Form.Item>

                    <Form.Item>
                        <Button type="primary" htmlType="submit" className="login-form-button">
                        ng nhp
                        </Button>
                        Hoc <Link to="/signup">ng k ngay!</Link>
                    </Form.Item>
                </Form>
            </div>
        </div>
    )
}

const mapDispatchToProps = (dispatch, props) => {
    return {
        handleLogin: (token) => {
            dispatch(actions.checkUser(token));
        }
    }
};

export default connect(null, mapDispatchToProps)(LoginComponent);