import React, { useState, useEffect } from 'react';
import { Table, Space, Button, Modal, Form, Input, Select, message, Tooltip, Popconfirm } from 'antd';
import { connect } from 'react-redux';
import * as action from '../../../../redux/actions';

const TableUsers = ({ users, handleUpdateUser, handleDeleteUser, searchText, userLogin }) => {
    const successUpdate = () => {
      message.success('Cp nht thnh cng');
    };

    const successDelete = () => {
      message.success('Xa thnh cng');
    };
  
    const columns = [
        {
            title: 'Tn ti khon',
            key: 'userName',
            render: (item) => (
              <Tooltip title={item.id}>
                  <p>{item.username}</p>
              </Tooltip>
            )
        },
        {
          title: 'H tn',
          dataIndex: 'name',
          key: 'name'
        },
        {
          title: 'a ch',
          dataIndex: 'address',
          key: 'address',
        },
        {
            title: 'S in thoi',
            dataIndex: 'phone',
            key: 'phone',
        },
        {
            title: 'Quyn ngi dng',
            dataIndex: 'idRole',
            key: 'idRole',
            render: (item) => {
              if(item === '0') {
                return <p>Admin</p>
              } else if(item === '2') {
                return <p>Ging Vin</p>
              } 
              return <p>Hc Vin</p>
            }
        },
        {
          title: 'Chc nng',
          key: 'action',
          render: (item, record) => (
            <Space size="middle" style={{width:'100%'}}>
                <Button type="primary" block onClick={() => showModalUpdateUser(item)}>Chnh sa</Button>
                { item.id === userLogin.id ?
                    <></>
                  :
                    <Popconfirm
                      title="Bn chc chn mun xa ngi dng ny?"
                      onConfirm={async() => {
                        await handleDeleteUser(JSON.parse(localStorage.getItem('token')).token,{id:item.id});
                        await successDelete();
                      }}
                      onCancel={(e) => console.log(e)}
                      okText="C"
                      cancelText="Hy"
                    >
                      <Button type="primary" danger block>Xa</Button>
                    </Popconfirm>
                }
            </Space>
          ),
        },
    ];

    const renderUsers = () => {
      let result = [];
      for(let i = 0; i < users.length; i++) {
        let item = {
          key:i,
          ...users[i]
        }
        if(item.name.toLowerCase().trim().indexOf(searchText.toLowerCase().trim()) !== -1) {
          result.push(item);
        }
      }
      return result;
    }
    const data = [...renderUsers()];

  // panigation
  // const [selectedRowKeys, setSelectedRowKeys] = useState([]);
  // const onSelectChange = selectedRowKeys => {
  //   console.log('selectedRowKeys changed: ', selectedRowKeys);
  //   setSelectedRowKeys({ selectedRowKeys });
  // };
  //   const rowSelection = {
  //     selectedRowKeys,
  //     onChange: onSelectChange,
  //   };
  // console.log(rowSelection);
  // end panigation

  // modal

  const [modalStyle, setModalStyle] = useState({
      visible: false,
      loading: false,
  });
  const { visible, loading } = modalStyle;

  const handleCancel = async() => {
    await form.resetFields();
    setModalStyle({
        ...modalStyle,
        visible: false,
    });
};

  const showModalUpdateUser = async(item) => {
      await form.setFieldsValue({
        idUpdateUser:item.id,
        userNameUpdateUser:item.username,
        nameUpdateUser: item.name,
        passwordUpdateUser: item.password,
        phoneUpdateUser: item.phone,
        addressUpdateUser: item.address,
        roleUpdateUser: item.idRole
      });
      setModalStyle({
          ...modalStyle,
          visible: true,
      });
  };

  const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 7 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 17 },
      },
  };
  

  const [form] = Form.useForm();

  const onFinish = async(values) => {
      await handleUpdateUser(JSON.parse(localStorage.getItem('token')).token,values);
      setModalStyle({
          ...modalStyle,
          loading: true,
      });
      setTimeout(async() => {
          await setModalStyle({
              ...modalStyle,
              visible: false,
              loading: false,
          });
        await successUpdate();
      }, 1000);
  };

    // end modal

    return (
        <div>
            <Table pagination={{ pageSize: 5 }} size="middle" columns={columns} dataSource={data} className="table__users"/>
            <Modal
                title="Cp nht ngi dng"
                visible={visible}
                footer=""
                onCancel={handleCancel}
                className="modal__updateCourse"
                >
                <Form
                    {...formItemLayout}
                    form={form}
                    name="updateUser"
                    onFinish={onFinish} 
                    scrollToFirstError
                >
                    <Form.Item
                        name="idUpdateUser"
                        hidden
                    >
                    </Form.Item>
                    <Form.Item
                        name="userNameUpdateUser"
                        label="Tn ti khon"
                        rules={[
                        {
                            type: 'string',
                            message: 'Nhp tn ti khon',
                        },
                        {
                            required: true,
                            message: 'Vui lng nhp tn ti khon!',
                        },
                        ]}
                    >
                        <Input placeholder="Nhp tn ti khon"/>
                    </Form.Item>
                    <Form.Item
                        name="passwordUpdateUser"
                        label="Mt khu"
                        rules={[
                        {
                            type: 'string',
                            message: 'Nhp mt khu',
                        },
                        {
                            required: true,
                            message: 'Vui lng nhp mt khu!',
                        },
                        ]}
                    >
                        <Input.Password placeholder="Nhp mt khu" />
                    </Form.Item>
                    <Form.Item
                        name="nameUpdateUser"
                        label="H tn"
                        rules={[
                        {
                            type: 'string',
                            message: 'Nhp Tn ngi dng',
                        },
                        {
                            required: true,
                            message: 'Vui lng nhp Tn ngi dng!',
                        },
                        ]}
                    >
                        <Input placeholder="Nhp tn ngi dng"/>
                    </Form.Item>
                    <Form.Item
                        name="phoneUpdateUser"
                        label="S in thoi"
                        rules={[
                        {
                            type: 'string',
                            message: 'Nhp S in thoi',
                        },
                        {
                            required: true,
                            message: 'Vui lng nhp S in thoi!',
                        },
                        ]}
                    >
                        <Input placeholder="Nhp S in thoi"/>
                    </Form.Item>
                    <Form.Item
                        name="addressUpdateUser"
                        label="a ch"
                        rules={[
                        {
                            type: 'string',
                            message: 'Nhp a ch',
                        },
                        {
                            required: true,
                            message: 'Vui lng nhp a ch!',
                        },
                        ]}
                    >
                        <Input placeholder="Nhp a ch" />
                    </Form.Item>
                    <Form.Item 
                      label="Quyn"
                      name="roleUpdateUser"
                    >
                      <Select>
                        <Select.Option value="0">Admin</Select.Option>
                        <Select.Option value="1">Hc Vin</Select.Option>
                        <Select.Option value="2">Ging Vin</Select.Option>
                      </Select>
                    </Form.Item>
                    <Form.Item>
                        <Button key="back" onClick={handleCancel}>
                            Thot
                        </Button>,
                        <Button htmlType="submit" key="submit" type="primary" loading={loading}>
                            Cp nht
                        </Button>
                    </Form.Item>
                </Form>
            </Modal>

        </div>
    )
}

const mapStateToProps = state => {
  return {
    users: state.users,
    userLogin: state.userLogin
  }
}

const mapDispatchToProps = (dispatch, props) => {
  return {
    handleUpdateUser: (token, data) => {
      dispatch(action.updateUserAPI(token, data));
    },
    handleDeleteUser: (token, data) => {
      dispatch(action.deleteUserAPI(token, data));
    } 
  }
}

export default connect(mapStateToProps,mapDispatchToProps)(TableUsers);
