import React, { useState, useEffect } from 'react';

import { connect } from 'react-redux';
import * as action from '../../../redux/actions';

import { Descriptions } from 'antd';
import axios from 'axios';


const ProfileDetail = ({ userLogin, profile }) => {
    const [detail, setDetail] = useState({});

    const getDetailUser = () => {
        axios({
            url:'https://courses-project-api.herokuapp.com/getDetailUser',
            method:'GET',
            headers: {
                'Authorization': JSON.parse(localStorage.getItem('token')).token,
                "Content-Type": "application/json",
            }
        }).then(res => {
            console.log(res.data);
            setDetail({...res.data.detailUser});
        }).catch(err => console.log(err));
    }

    useEffect(() => getDetailUser(),[]);

    return (
        <div className="profile__detail">
             <Descriptions bordered column={{ xxl: 4, xl: 3, lg: 3, md: 3, sm: 2, xs: 1 }}>
                <Descriptions.Item label="Tn ti khon">{detail.username}</Descriptions.Item>
                <Descriptions.Item label="S in thoi">{detail.phone}</Descriptions.Item>
                <Descriptions.Item label="H Tn">{detail.name}</Descriptions.Item>
                <Descriptions.Item label="a ch">
                    {detail.address}
                </Descriptions.Item>
                <Descriptions.Item label="Quynt">{detail.roleName}</Descriptions.Item>
            </Descriptions>
        </div>
    )
}

const mapStateToProps = state => {
    return {
        userLogin: state.userLogin,
        profile: state.profile
    }
}

export default connect(mapStateToProps)(ProfileDetail);
